@import "./components/_variables";


#root
{
	.App.Mobile
	{
		.Header
		{
			height: auto;

			.MainHeader
			{
				margin-left: 0;
				font-size: 9vw;
				text-align: center;
				
				display: block;
				width: 100%;
			}

			.SearchBar
			{
				display: none;
			}

			.HeaderItems
			{
				float: none;
				position: relative;
				display: block;
				width: 100%;

				display: flex;
				flex-direction: column-reverse;
				
				pointer-events: none;

				.HeaderItemContainer
				{
					display: block;

					width: 100%;
					height: 50px;

					pointer-events: all;

					.HeaderItem
					{
						order: -10;
						width: 260px;
						background-color: transparent;

						left: 5vw;

						overflow: visible;
				
						.HeaderItemContent
						{
							color: white;
							width: 250px;
						}

						.HeaderItemIcon
						{
							left: -25px;
							width: 30px !important;
							height: 27px;
							filter: invert(100%);
						}
					}
				}

				&.Collapsed
				{
					flex-direction: row;
					pointer-events: all;

					.HeaderItemContainer
					{
						pointer-events: none;
						left: 0;
						width: 50px;
					}

					.HeaderItemContainer .HeaderItem .HeaderItemContent { display: none; }
					.HeaderItemContainer .HeaderItem .HeaderItemIcon { margin-left: 10px; }
				}
			}
		}

		.Items
		{
			.Item
			{
				width: 100%;
				height: 300px;

				.ItemFooter { opacity: 1; }
				.ItemFooter .ItemButton { font-size: 0.8rem; }
			}
		}

		.Tooltip
		{
			position: fixed;
			top: 0 !important; left: 0 !important; right: 0 !important;
			width: 94.5vw !important;
		}

		.ModalContainer
		{
			.Modal
			{
				.MoreInfoModal { max-width: 100vw; }

				.ClipboardModal
				{
					background: none;
					height: 100%;

					span
					{
						display: block;
						margin-top: 45vh;
					}
				}
			}
		}

		.Main .ProjectList
		{
			.Project
			{
				.ProjectDesc { font-size: 0.85rem; }

				.Slideshow
				{
					height: 200px;

					.Buttons
					{
						.Button
						{
							width: 20px;
							top: 47%;
						}
					}
				}

				.ProjectButtons
				{
					flex-direction: column;
					margin-right: 20px;
				}
			}
		}

		.TabsList
		{
			.TabButton
			{
				width: 18vw;
			}

			.TabButtonTitle
			{
				font-size: 1.2rem;
			}
		}

		.NoContentsMessage
		{
			font-size: 5vw;
		}

		.Footer
		{
			font-size: 2.7vw;
		}
	}
}